/* 상세 */
.board_view_top{padding:30px 30px 40px; font-size:0; border-top:2px solid #222; border-bottom:1px solid #e5e5e5;}
.board_view_top .txt{display:inline-block; width:calc(100% - 100px); margin-right:50px; vertical-align:top; transition:all 0.3s ease-in-out;}
.board_view_top.on .txt{width:calc(100% - 310px);}
.board_view_top .full{width:100%;}
.board_view_top h3{font-size:28px; font-weight:500; line-height:1.4;}
.board_view_top span{display:block; margin-top:15px; font-size:16px; color:#999; font-weight:500;}
.board_view_top .shareWrap{display:inline-block; width:50px; font-size:0; vertical-align:top; text-align:right; position:relative; right:0; transition:all 0.3s ease-in-out;}
.board_view_top.on .shareWrap{width:260px;}
.board_view_top .shareWrap img{width:50px;}
.board_view_top .shareWrap dt{width:190px; position:absolute; top:0; right:70px;}
.board_view_top .shareWrap dt a{display:inline-block; margin-right:20px; transform:scale(0.5) rotate(-45deg); opacity:0; transition:all 0.3s ease-in-out;}
.board_view_top .shareWrap dt a:first-of-type{transition-delay:0.1s;}
.board_view_top .shareWrap dt a:nth-of-type(2){transition-delay:0.2s;}
.board_view_top .shareWrap dt a:last-of-type{margin-right:0; transition-delay:0.3s;}
.board_view_top .shareWrap dt.on{display:inline-block;}
.board_view_top .shareWrap dt.on a{transform:scale(1) rotate(0deg); opacity:1;}
.board_view_top .shareWrap dt.on a:first-of-type{transition-delay:0.3s;}
.board_view_top .shareWrap dt.on a:nth-of-type(2){transition-delay:0.2s;}
.board_view_top .shareWrap dt.on a:last-of-type{margin-right:0; transition-delay:0.1s;}
.board_view_top .shareWrap dd{display:inline-block; cursor:pointer;}

